/*--------------------adjustment----------------------*/
*{
	box-sizing: border-box;
}
html, body{
	margin: 0;
	padding: 0;
}
body{
	font-family: $inter;
	font-weight: normal;
	font-size: 14px;
	min-width: 320px;
	position: relative;
	line-height: 17px;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
	color: $text;

	.lock & {
		overflow: hidden;
		touch-action: none;
		overflow-behavior: none;
	}
}

section{
	height: 100vh;
	// background: gray;
}
