// Прижать футер к низу

html, body{
	height: 100%;
}
.main{
	display: flex;
	flex-direction: column;
	height: 100%;
	.header{
		flex: 0 0 auto;
	}
	.main-content{
		flex: 1 1 auto;
	}
	.footer{
		flex: 0 0 auto;
		// margin-top: auto;
	}
}

// END Прижать футер к низу

ul, li{
	margin: 0;
	padding: 0;
}
input
input[type="text"],
input[type="email"],
input[type="tel"],
textarea{
	appearance: none;
}
.input,
input{
	outline: none;
	display: block;
	width: 100%;
}
textarea.input{
	resize: none;
	padding: 0 0;
}
//END input
.wrapper{
	margin: 0 auto;
	padding-left: $containerPaddings / 2 + px;
	padding-right: $containerPaddings / 2 + px;
	max-width: $maxContainerWidth + px;
	// @include row-flex();
	// @include wrapper();
	// outline: 1px solid;
}
// classes for smartgrid(c) structure
.wrapper-smart{
	@include wrapper();
	// outline: 1px solid;
}
.wrapper-flex{
	@include row-flex();
}
.column{
	@include col();
	@include size(3);	
}

.hidden{
	display: none;
}

.ibg{
background-position: center; 
background-size: cover; 
background-repeat: no-repeat; 
position: relative;
padding-bottom: 52.25%;
}

.ibg img{
	width: 100%; 
	height: 100%; 
	position: absolute; 
	top: 0; 
	left: 0; 
	opacity: 0; 
	// visibility: hidden;
}
._prod-img {
	position: relative;
	padding-bottom: 80%;
	img{
		@include absolute-center;
	}
}
._fit{
	padding-bottom: 80%;
	position: relative;
}
._fit ._fit-img{//_fit - class for object-fit
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0;
	right: 0;
	object-fit: cover; 
	object-position: center center;
}

.bd-filter-fallback{// определение поддержки блюра без джаваскрипт
	@supports (backdrop-filter: blur(10px)) or (--webkit-backdrop-filter: blur(10px)){// определение поддержки блюра без джаваскрипт
			background-color: rgba(255, 255, 255, .72);
			backdrop-filter: blur(31px);
		}
}
