.body-cover{
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background-color: rgba(#000, .5);
	z-index: 9;
	visibility: hidden;
	opacity: 0;
	transition: all .3s;

	@include max-screen($md){
		z-index: 11;
		
	}

	.covered &{
		visibility: visible;
		opacity: 1;
	}
}
