.wrapper.rating{
	font-family: $navbl;
	// rating на странице написания отзва от товаре
	.simple-rating{
		@include simple-rating(rgba($text, .2), $yellow, 35px, 25px, '\e901');
	}
	// rating на в карточке товара
	.crystal-rating{
		@include crystal-rating($gray-l, $yellow, 12, 4, '\e901');
	}
}
.my-slide{
